<template>
    <div>
        <a-alert :message = broName banner />
    </div>
</template>
<script>
export default {
    name: 'Check',
    data(){
        return {
            broName : 'Runing',
            is360 : false,
            isIE : false,
            isFirefox : false,
            isChrome : false,
            isEdge : false,
            select : 2
        }
    },
    mounted: function(){
        var _this = this;
        this.init();
        this.CallBack();
    },
    methods:{
        init: function(){
            var _this = this;
            var str = '';
            var strStart = 0;
            var strStop = 0;
            var arr = new Array();
            var temp = '';
            var tip = '';

            var userAgent = window.navigator.userAgent; //包含以下属性中所有或一部分的字符串：appCodeName,appName,appVersion,language,platform

            /*alert(userAgent);*/

            //FireFox
            if (userAgent.indexOf('Firefox') != -1) {
                _this.isFireFox = true;
                /*broName = 'FireFox浏览器';*/
                strStart = userAgent.indexOf('Firefox');
                temp = userAgent.substring(strStart);
                _this.broName = temp.replace('/', '版本号')
                _this.select = 3;
            }

            //Edge
            if (userAgent.indexOf('Edge') != -1) {
                _this.isEdge = true;
                /*broName = 'Edge浏览器';*/
                strStart = userAgent.indexOf('Edge');
                temp = userAgent.substring(strStart);
                _this.broName = temp.replace('/', '版本号');
                tip = '您的浏览器版本是 ' + _this.broName + ' , 建议您使用 Chrome 浏览器以保证正常使用';
                _this.broName = tip;
                _this.select = 5;
            }

            //IE浏览器
            if (userAgent.indexOf('NET') != -1 && userAgent.indexOf("rv") != -1) {
                _this.isIE = true;
                /*broName = 'IE浏览器'; */
                strStart = userAgent.indexOf('rv');
                strStop = userAgent.indexOf(')');
                temp = userAgent.substring(strStart, strStop);
                _this.broName = temp.replace('rv', 'IE').replace(':', '版本号');
                tip = '您的浏览器版本是 ' + _this.broName + ' , 建议您使用 Chrome 浏览器以保证正常使用';
                _this.broName = tip;
                _this.select = 2;
            }

            //360极速模式可以区分360安全浏览器和360极速浏览器
            if (userAgent.indexOf('WOW') != -1 && userAgent.indexOf("NET") < 0 && userAgent.indexOf("Firefox") < 0) {
                if(navigator.javaEnabled()){
                    _this.is360 = true;
                    _this.broName = '360安全浏览器-极速模式';
                }else{
                    _this.is360 = true;
                    _this.broName = '360极速浏览器-极速模式';
                } 
                _this.select = 1;
            }

            //360兼容
            if (userAgent.indexOf('WOW') != -1 && userAgent.indexOf("NET") != -1 && userAgent.indexOf("MSIE") != -1 && userAgent.indexOf("rv") < 0) {
                _this.is360 = true;
                _this.broName = '360兼容模式';
                _this.select = 1;
            }

            //Chrome浏览器
            if (userAgent.indexOf('WOW') < 0 && userAgent.indexOf("Edge") < 0) {
                _this.isChrome = true;
                /*broName = 'Chrome浏览器';*/
                strStart = userAgent.indexOf('Chrome');
                strStop = userAgent.indexOf('Safari');
                temp = userAgent.substring(strStart, strStop);
                _this.broName = temp.replace('/', '版本号');
                _this.select = 4;
            }
        },
        CallBack: function(){
            if((this.select === 1)||(this.select === 3)||(this.select === 4))
                this.$emit("callBack", false)
            else
                this.$emit("callBack", true)
        }
    }
}
</script>
<style>

</style>



